* {
  font-family: Roboto, sans-serif; }

header {
  position: relative; }

.header__block {
  position: fixed;
  width: 100%; }

nav {
  height: 50px;
  background-color: #212121; }

.subheader {
  height: 87px;
  background-color: rgba(0, 0, 0, 0.6);
  position: absolute;
  width: 100%; }

.menu {
  padding: 12px 70px;
  margin: 0;
  list-style-type: none;
  display: flex;
  justify-content: space-between;

  .menu__link {
    color: #FFFFFF;
    font-weight: 300;
    font-size: 15px;
    line-height: 15px;
    text-decoration: none; }

  .menu__item {
    border-right: 2px dotted black;
    padding-right: 45px; } }

.subheader {
  .subheader__logo {
    display: block;
    max-width: 100%; }

  .subheader__official {
    color: #FFFFFF;
    font-weight: 200;
    opacity: 0.5;
    font-size: 13px;
    margin-top: 6px; }

  .subheader__phone {
    color: #FFFFFF;
    font-weight: 200;
    font-size: 12px;
    text-align: right; }

  .subheader__number {
    display: block;
    color: #FFFFFF;
    font-size: 22px;
    text-align: right; }

  .subheader__block {
    padding: 16px 0; }

  .subheader__btn {
    width: 100%;
    height: 90%;
    background: linear-gradient(0deg, #1EACC7 0%, #4CE2FF 100%);
    box-shadow: inset 0px -3px 0px rgba(0, 0, 0, 0.12);
    border-radius: 4px;
    border: none;
    color: #FFFFFF;
    font-size: 14px;
    text-align: center;
    text-transform: uppercase;
    text-shadow: 1px 1px 0px rgba(0, 0, 0, 0.21); } }

.promo {
  min-height: 800px;
  background: url('../images/uber_bg.png') center (center / cover) no-repeat;
  padding: 151px 0 186px 0;

  .promo__header {
    color: #FFFFFF;
    font-weight: 900;
    font-size: 60px;
    text-align: center;
    text-transform: uppercase; }

  .promo__subheader {
    color: #FFFFFF;
    font-weight: 500;
    font-size: 36px;
    text-align: center;
    text-transform: uppercase; }

  .promo__descr {
    margin-top: 16px;
    color: #FFFFFF;
    font-size: 18px;
    font-weight: 300;
    text-align: center; }

  .promo__btn {
    display: block;
    width: 259px;
    height: 63px;
    margin: 0 auto;
    margin-top: 115px;
    background: linear-gradient(0deg, #1EACC7 0%, #4CE2FF 100%);
    box-shadow: inset 0px -4px 0px rgba(0, 0, 0, 0.12);
    border-radius: 4px;
    color: #FFFFFF;
    font-size: 18px;
    line-height: 63px;
    text-align: center;
    text-transform: uppercase;
    text-shadow: 1px 1px 0px rgba(0, 0, 0, 0.21);
    border: none; } }

