.work__reasons {
  .reason__item {
    list-style-type: none;
    margin-top: 60px;
    max-height: 116px; }

  .reason__img__elipse {
    display: flex;
    width: 126px;
    height: 126px;
    border-radius: 100%;
    background: #1EACC7;
    justify-content: center;
    align-items: center;
    float: left; } }

.test {
  margin-left: 25px; }

.work__reasons {
  .reason__subheader {
    color: #1EACC7;
    display: inline-block;
    margin-left: 25px;
    font-size: 17px; }

  .reason__descr {
    color: #202020;
    display: inline-block;
    margin-left: 25px;
    font-weight: 300;
    font-size: 15px; }

  .work__reason__header {
    color: #222222;
    font-weight: 700;
    font-size: 38px;
    text-transform: uppercase;
    text-align: center;
    margin-top: 77px; } }
